<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <CategoryList :id="categoryId" :request="request" @handleOperate="handleOperate" ref="list"/>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import CategoryList from '@/components/category/list/Index'
    import {getPublishedArticleList, getAuditUserArticleList, getArticleList, deleteArticle} from '@/api/article'

    export default {
        components: {
            TopButtons, CategoryList
        },
        data() {
            return {
                categoryId: 11,
                request: getArticleList,
            }
        },
        methods: {
            handleOperate(type, row) {
                let id = row.id;
                switch (type) {
                    case 'check':
                        this.$router.push({name: '在线学习查看', params: {id: id}})
                        break;
                    case 'delete':
                        deleteArticle(id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.list.reFetch();
                            });
                        break;

                }
            },
        }
    }
</script>
